body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color:	#CAE4DB;
  color: #00303F;
}

.weatherApp {
  text-align: center;
  height: 100vh;
}


@media (min-width: 1024px){
  html{
    background-color:	#CAE4DB;
    color: #00303F;
  }
  
  .weatherApp {
    text-align: center;
    height: 100vh;
  }
}




.submitButton{
    font-size: 1em;
    border-radius: 6px;
    padding: 0.6% 5%;
    height: 5%;
    background-color:#CAE4DB;
    color: #00303F;
    margin-left: 1%;
}

@media (min-width: 1024px){
    .submitButton{
        font-size: 3em;
        border-radius: 6px;
        padding: 0.6% 5%;
        height: 5%;
        background-color:#CAE4DB;
        color: #00303F;
        margin-left: 1%;
    }
}
.textInput{
    width: 40%;
    font-size: 1em;
    border-radius: 6px;
    text-align: center;
    font-size: 1em;
}

@media (min-width: 1024px){
    .textInput{
        width: 40%;
        font-size: 2.7em;
        border-radius: 6px;
        text-align: center;
        margin-bottom: 2%;
        margin-right: 2%;
        
    }
}
.formContainer{
    background-color: #7A9D96;
    color: white;
    padding: 0.3% 0.6%;	
    
}

.iconClockContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

time{
    padding-top: 1%;
    font-size: 1em;
}

.label{
    font-size: 1em;
    text-align: left;
    margin-left: 1%;
    margin-top: 1%;
}

.textAndSubmitComponents{
    display: inline-block;
}

@media (min-width: 1024px){
    .formContainer{
        margin-left: 25%;
        margin-right: 25%;
        background-color: #7A9D96;
        color: white;
        padding: 0.3% 0.6%;	
        
    }
    
    .iconClockContainer{
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
    
    time{
        padding-top: 1%;
        font-size: 3em;
    }
    
    .label{
        font-size: 3em;
        text-align: left;
        margin-left: 1%;
        margin-top: 1%;
    }
    
    .textAndSubmitComponents{
        display: inline-block;
    }
}
.unitButton{
    font-size: 1em;
    border-radius: 6px;
    background-color:#CAE4DB;
    color: #00303F;
    margin-right: 3%;
    padding: 0.6% 5%;
}

@media (min-width: 1024px){
    .unitButton{
        font-size: 2em;
        border-radius: 6px;
        background-color:#CAE4DB;
        color: #00303F;
        margin-right: 3%;
        padding: 0.6% 5%;
    }
}
h4{
    display: inline;
    font-size: 1em;
}

.cityName{
    font-size: 1em;
}



.forecastContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-left: 20%;
    margin-right: 20%;

}

@media (min-width: 1024px){
    h4{
        display: inline;
        font-size: 50px;
    }
    
    .cityName{
        font-size: 70px;
    }
    
    
    
    .forecastContainer{
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        margin-left: 20%;
        margin-right: 20%;
    
    }
}
.error{
    background-color:  #800000;
    color: white;
    font-size: 50px;
    margin: 1% 25% 0;
}
